*{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
html, body, #root{
    height: 100%;
}
span, label, div, a{
    font-size: 14px;
}
.App {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    margin: 0 auto;
    overflow: hidden;
}


.content{
    display: flex;
    width: 100%;
}
.pages{
    background: white;
    /*padding: 12px;*/
    /*height: calc(100vh - 78px);*/
    height: calc(100vh - 42px);
    width: 80%;
    overflow: auto;
}

